						alert(' Canvas');
						return;
					}

					//
					initPage(PathSimplifier);
				});

				function initPage(PathSimplifier) {
					//
					var pathSimplifierIns = new PathSimplifier({
						zIndex: 100,
						map: map, //
						getPath: function(pathData, pathIndex) {
							//[AMap.LngLat, AMap.LngLat...]  [[lng|number,lat|number],...]
							return pathData.path;
						},
						getHoverTitle: function(pathData, pathIndex, pointIndex) {
							//
							if(pointIndex >= 0) {
								//
								return pathData.name + ':' + pointIndex + '/' + pathData.path.length;
							}
							//
							return pathData.name + '' + pathData.path.length;
						},
						renderOptions: {
							//
							pathLineStyle: {
								strokeStyle: 'white',
								lineWidth: 4,
								dirArrowStyle: true
							}
						}
					});

					//
					pathSimplifierIns.setData([{
						name: '',
						path: that.mydata
					}]);

					//
					var navg0 = pathSimplifierIns.createPathNavigator(0, //1
						{
							loop: true, //
							speed: 500,
							pathNavigatorStyle: {
								width: 24,
								height: 24,
								//
								content: PathSimplifier.Render.Canvas.getImageContent('../../../static/plane.png', onload, onerror),
								strokeStyle: null,
								fillStyle: null,
								//
								pathLinePassedStyle: {
									lineWidth: 6,
									strokeStyle: '#1e81d2',
									dirArrowStyle: {
										stepSpace: 15,
										strokeStyle: 'white'
									}
								}
							}
						});

					navg0.start();
				}
			},
			back: function() {
				this.$router.back()
			},
			timeshow: function(type) {
				var that = this
				plus.nativeUI.pickDate(function(e) {
					var d = e.date;
					if(type == 0) {
						that.mydate = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate()
					} else {
						that.mydate = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate()
					}
				}, function(e) {
					console.log("" + e.message);
				});
			},
			startchang: function(date, formatDate) {
				this.date = formatDate
			},
			opennew: function(target) {
				this.$router.push({
					name: target
				})
			}
		},
